import React, { memo } from 'react';
import { Link } from 'gatsby';
import { Grid, Chip, Box, useTheme } from '@mui/material';

import Markdown from './Markdown';
import RelatedPosts from './RelatedPosts';
import PostHeader from './PostHeader';
import Languages from './Languages';
import { Post } from 'src/models/fragments';
import { usePageRoute } from 'hooks/usePageRoute';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';

interface Props {
  post: Post;
  viewCount?: number;
  otherLangPosts: Array<Pick<Post, 'fields'>>;
}
const PostContents = ({ post, viewCount, otherLangPosts }: Props) => {
  const { spacing } = useTheme();

  const { getTagPath } = usePageRoute();
  const { frontmatter, html } = post;

  const title = frontmatter?.title ?? '';
  const categories = frontmatter?.categories ?? [];
  const date = frontmatter?.date ?? '';
  const tags = frontmatter?.tags ?? [];

  const thumbnail = frontmatter?.jumbotron?.childImageSharp;

  return (
    <>
      {thumbnail && (
        <Box sx={{ marginBottom: spacing(2) }}>
          <GatsbyImage image={getImage(thumbnail)!} alt={title} title={title} />
        </Box>
      )}
      <PostHeader title={title ?? ''} date={date} viewCount={viewCount} categories={categories} />
      {otherLangPosts.length > 0 && (
        <Box sx={{ margin: `${spacing(4)} 0` }}>
          <Languages otherLangPosts={otherLangPosts} />
        </Box>
      )}
      <Markdown html={html ?? ''} />
      <Box component="section" sx={{ marginTop: spacing(4) }}>
        <RelatedPosts tags={tags ?? []} currentPost={post} />
      </Box>
    </>
  );
};

export default memo(PostContents);
